import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { LogQueryComponent } from './logQuery.component';
import { BusinessManageLogComponent } from './business-manage-log/business-manage-log.component';
import { SystemLoginStatisticComponent } from './system-login-statistic/system-login-statistic.component';

const routes: Routes = [
  {
    path: '', component: LogQueryComponent,
    children: [
      { path: 'business-manage-log', component: BusinessManageLogComponent },
      { path: 'system-login-statistic', component: SystemLoginStatisticComponent },
    ]
  },
];

export const LogQueryRouting: ModuleWithProviders = RouterModule.forChild(routes);
