import { Component, OnInit } from '@angular/core';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message, SelectItem } from 'primeng/primeng';

@Component({
  selector: 'app-business-manage-log',
  templateUrl: './business-manage-log.component.html',
  styleUrls: ['./business-manage-log.component.scss'],
  providers: [CustomeHttpService]
})
export class BusinessManageLogComponent implements OnInit {
  operator: string;//操作用户ID
  custNo: string;//查询的客户号
  msgs: Message[] = [];//提示信息
  tableData: Array<any> = [];
  first: number = 0;//分页控制
  rows: number;//当前页条数
  currentPage: number;//当前页
  pageSize: string;
  pageNum: string;
  total: number;
  messageDisplay: boolean;
  headerTitle: string;
  messageValue: string;//报文
  constructor(
    public customeHttpService: CustomeHttpService
  ) { }

  ngOnInit() {
    this.pageSize = '10';
    this.pageNum = '1';
    this.query();
  }

  clickQuery() {
    this.pageNum = '1';
    this.first = 0;
    this.query();
  }
  query() {
    let params = {
      operator: this.operator,
      custNo: this.custNo,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    }
    this.customeHttpService.queryOperationHistory(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.opList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }

  //重置
  reset() {
    this.operator = '';
    this.custNo = '';
  }

  //入参报文
  inMessage(item) {
    this.messageDisplay = true;
    this.headerTitle = '入参报文';
    this.messageValue = item.inMessage;
  }

  //出参报文
  outMessage(item) {
    this.messageDisplay = true;
    this.headerTitle = '出参报文';
    this.messageValue = item.outMessage;
  }

  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.pageSize = rows + '';
    //当前页
    let currentPage = event.page + 1 + '';
    this.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.query();
  }
}
