import { Component, OnInit ,Input} from '@angular/core';

@Component({
  selector: 'message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.scss']
})
export class MessageComponent implements OnInit {
  @Input() inValue:any;
  message:string;
  constructor() { }

  ngOnInit() {
    this.message=this.inValue;
  }

}
