import { Component, OnInit } from '@angular/core';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message, SelectItem } from 'primeng/primeng';

@Component({
  selector: 'app-system-login-statistic',
  templateUrl: './system-login-statistic.component.html',
  styleUrls: ['./system-login-statistic.component.scss'],
  providers: [CustomeHttpService]
})
export class SystemLoginStatisticComponent implements OnInit {
  msgs: Message[] = [];//提示信息
  pageSize: string;
  pageNum: string;
  operator: string;//操作用户ID
  tableData: any;
  total: number;
  first:number=0;
  constructor(
    public customeHttpService: CustomeHttpService
  ) { }

  ngOnInit() {
    this.pageSize = '10';
    this.pageNum = '1';
    this.query();
  }

  clickQuery() {
    this.pageNum = '1';
    this.first = 0;
    this.query();
  }
  query() {
    let params = {
      operator: this.operator,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    }
    this.customeHttpService.queryCurrentOperatorInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.opList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: '调用服务失败！' })
    })
  }

  reset() {
    this.operator = '';
  }

  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.pageSize = rows + '';
    //当前页
    let currentPage = event.page + 1 + '';
    this.pageNum = currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.query();
  }
}
